.cart {
    padding-right: 10px;
    font-size: 0;
    text-decoration: none;

    &__inner {
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        width: 50px;
        height: 50px;
        border-radius: 50%;
        background-color: $color-Eucalyptus;
        font-size: 0;
    }

