            width: 26px;
        }
    }

    &__numWrap {
        position: absolute;
        top: 0;
        right: -10px;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 20px;
        height: 20px;
        border-radius: 50%;
        border: 1px solid $color-Eucalyptus;
        background-color: $color-primary;
    }

    &__num {
        text-transform: uppercase;
        font-weight: 600;
        @include font($font-main, 10px, rgb(7, 45, 22));
    }
}